<template>
  <div class="entrySec">
    <Top></Top>
    <Logo></Logo>
    <section id="content">
      <!-- 主體內容 -->
      <div class="main type-area">
        <div class="general">
          <!-- 頂部 -->
          <div class="gen_title">
            <span>模治檢具订单信息服務平台【厂商入駐】</span>
            <span>数据截至到2019-08-31 24:00:00</span>
          </div>
          <!-- 交易總額 -->
          <div class="gen_amount">
            <span>平台交易總額</span>
            <div class="gen_am_number">
              <span>￥</span>
              <span>95,371,863</span>
              <span>元</span>
            </div>
            <div class="gen_am_info">
              <span>需求總量: 13,537個</span>
              <span>加工廠商: 159個</span>
              <span>需求廠商: 159個</span>
            </div>
          </div>
        </div>
        <!-- 標語 -->
        <div class="slogan" @click="test()">加入我们,让您遇见更好的伙伴</div>
        <!-- 信息填寫框 -->
        <div class="formBox">
          <div class="form_title">
            <span>填寫資料</span>
            <div class="tit_describe">
              <span>我是{{category == 'recv' ? '接單方':'需求方'}}</span>
              <span>承接模治檢具加工、設計。</span>
            </div>
          </div>
          <div class="form_info">
            <!-- 左表單 -->
            <div class="formLeft">
              <!-- form表單 -->
              <el-form ref="formOne" status-icon :model="form" :rules="rules" label-width="80px">
                <!-- LOGO -->
                <el-form-item label="LOGO:" class="logo">
                  <el-image style="width: 40px; height: 40px" :src="form.logoUrl" fit="cover"></el-image>
                  <span @click="upImg">上傳</span>
                  <input ref="fileInp" type="file" class="dis_none" @change="getFile" />
                  <span>200px*200px以上，不超過1M</span>
                </el-form-item>
                <!-- 賬號名稱 -->
                <el-form-item label="賬號名稱:" class="name" prop="name">
                  <el-input v-model="form.name" auto-complete="off"></el-input>
                </el-form-item>
                <!-- 密码 -->
                <el-form-item label="密码:" class="psd" prop="psd">
                  <el-input v-model="form.psd" show-password></el-input>
                </el-form-item>
                <!-- 确认密码 -->
                <el-form-item label="确认密码:" class="conpsd" prop="conpsd">
                  <el-input v-model="form.conpsd" show-password></el-input>
                </el-form-item>
                <!-- 所屬廠區 -->
                <el-form-item label="所屬廠區:" class="factory" prop="factory">
                  <el-select v-model="form.factory" @click.native="_query_factory_list()">
                    <el-option
                      v-for="(item, index) in factory_list"
                      :key="item.list_order"
                      :label="item.name || index"
                      :value="item.pkid"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <!-- 次集團 -->
                <el-form-item label="次集團:" class="group" prop="group">
                  <el-select v-model="form.group" @click.native="_query_SECN_CMPY_list()">
                    <el-option
                      v-for="(item, index) in SECN_CMPY_list"
                      :key="item.pkid || index"
                      :label="item.name"
                      :value="item.pkid"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <!-- 事業群 -->
                <el-form-item label="事業群:" class="bussiness" prop="bussiness">
                  <el-select v-model="form.bussiness" @click.native="_query_ENTRPS_GROUP_list()">
                    <el-option
                      v-for="(item, index) in ENTRPS_GROUP_list"
                      :key="item.pkid || index"
                      :label="item.name"
                      :value="item.pkid"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <!-- 處 -->
                <el-form-item label="處:" class="bureau" prop="bureau">
                  <el-select v-model="form.bureau" @click.native="_query_PD_OFFICE_list()">
                    <el-option
                      v-for="(item, index) in PD_OFFICE_list"
                      :key="item.pkid || index"
                      :label="item.name"
                      :value="item.pkid"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <!-- 單位 -->
                <el-form-item label="單位:" class="departName" prop="departName">
                  <el-input v-model="form.departName"></el-input>
                </el-form-item>
              </el-form>
            </div>
            <!-- 右表單 -->
            <div class="formRight">
              <el-form ref="formTwo" status-icon :model="form" :rules="rules" label-width="80px">
                <!-- 交易法人 -->
                <el-form-item label="交易法人:" class="corporate" prop="corporate">
                  <el-input v-model="form.corporate"></el-input>
                </el-form-item>
                <!-- 費用代碼 -->
                <el-form-item label="費用代碼:" class="costCode" prop="costCode">
                  <el-input v-model="form.costCode"></el-input>
                </el-form-item>
                <!-- 加工範圍 -->
                <el-form-item
                  v-if="category == 'send'"
                  label="加工範圍:"
                  class="processRange"
                  prop="processRange"
                >
                  <el-input v-model="form.processRange"></el-input>
                </el-form-item>
                <!-- 業務經理 -->
                <el-form-item label="業務經理:" class="manager" prop="manager">
                  <el-input v-model="form.manager"></el-input>
                </el-form-item>
                <!-- 聯繫電話 -->
                <el-form-item label="聯繫電話:" class="tel" prop="tel">
                  <el-input v-model.number="form.tel"></el-input>
                </el-form-item>
                <!-- Email -->
                <el-form-item label="Email:" class="email" prop="email">
                  <el-input v-model="form.email"></el-input>
                </el-form-item>
                <!-- 簡介 -->
                <el-form-item label="簡介:" class="intro" prop="intro">
                  <el-input type="textarea" v-model="form.intro"></el-input>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <!-- 按鈕 -->
          <div class="form_chooseBtn" @click="submit('formOne','formTwo')">
            <router-link to>
              <svg width="265px" height="65px" version="1.1" xmlns="http://www.w3.org/2000/svg">
                <polygon
                  points="0,5 5,0 260,0 265,5 265,60 260,65 5,65 0,60 0,5"
                  style="fill:none;stroke:#0096FF;stroke-width:1"
                />
                <rect x="2" y="2" width="261" height="61" style="fill:#0096FF;" />
                <polyline
                  points="10,13 10,6 6,6 6,10 13,10 13,6 252,6 252,10 259,10 259,6 255,6 255,13 259,13 259,52 255,52 255,59 259,59 259,55 252,55 252,59 13,59 13,55 6,55 6,59 10,59 10,52 6,52 6,14 11,14"
                  style="fill:none;stroke:#3FB0FF;stroke-width:2;"
                />
              </svg>
              <span>提交審核</span>
            </router-link>
          </div>
        </div>
      </div>
    </section>
    <Footer></Footer>
  </div>
</template>

<script>
import Top from "../../components/Top"
import Logo from "../../components/Logo"
import Footer from "../../components/Footer"

// 引入請求
import {
  ck_user_is_exist,
  query_factory_list,
  query_SECN_CMPY_list,
  query_ENTRPS_GROUP_list,
  query_PD_OFFICE_list,
  register
} from "@/api/formInfo"
import { file_upload } from "@/api/file"

export default {
  data: function() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"))
      } else if (value !== this.form.psd) {
        callback(new Error("两次输入密码不一致!"))
      } else {
        callback()
      }
    }
    var validatorName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请輸入名稱"))
      } else if (value.length < 3 || value.length > 10) {
        callback(new Error("长度在 3 到 10 个字符"))
      } else {
        // 如果 value 不為空，發起請求判斷重複
        this.ck_user(this.form.name, callback)
      }
    }
    return {
      // 表單數據
      form: {
        // logo url
        logoUrl: require("../../assets/imgs/merchant/LOGO.png"),
        // 賬號名稱
        name: "",
        // 密码
        psd: "",
        // 确认密码
        conpsd: "",
        // 廠區
        factory: "",
        // 次集團
        group: "",
        // 事業群
        bussiness: "",
        // 處
        bureau: "",
        // 單位名
        departName: "",
        // 交易法人
        corporate: "",
        // 費用代碼
        costCode: "",
        // 加工範圍
        processRange: "",
        // 業務經理
        manager: "",
        // 聯繫電話
        tel: "",
        // email
        email: "",
        // 簡介
        intro: ""
      },
      // 表單校驗
      rules: {
        // 賬號名稱
        name: [
          {
            required: true,
            validator: validatorName,
            trigger: "blur"
          }
        ],
        // 密码
        psd: [{ required: true, message: "请输入密碼", trigger: "blur" }],
        // 确认密码
        conpsd: [{ required: true, validator: validatePass, trigger: "blur" }],
        // 廠區
        factory: [{ required: true, message: "请選擇廠區", trigger: "change" }],
        // 次集團
        group: [{ required: true, message: "请選擇廠區", trigger: "change" }],
        // 事業群
        bussiness: [
          { required: true, message: "请選擇廠區", trigger: "change" }
        ],
        // 處
        bureau: [{ required: true, message: "请選擇廠區", trigger: "change" }],
        // 單位名
        departName: [
          { required: true, message: "單位名稱不能為空", trigger: "blur" },
          { min: 4, max: 15, message: "长度在 4 到 15 个字符", trigger: "blur" }
        ],
        // 交易法人
        corporate: [
          { required: true, message: "交易法人不能為空", trigger: "blur" }
        ],
        // 費用代碼
        costCode: [
          { required: true, message: "費用代碼不能為空", trigger: "blur" }
        ],
        // 加工範圍
        processRange: [
          { required: true, message: "加工範圍不能為空", trigger: "blur" }
        ],
        // 業務經理
        manager: [
          { required: true, message: "業務經理不能為空", trigger: "blur" }
        ],
        // 聯繫電話
        tel: [
          { required: true, message: "號碼不能为空" },
          { type: "number", message: "號碼必须为数字值" }
        ],
        // email
        email: [
          { required: true, message: "邮箱地址不能為空", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          }
        ],
        // 簡介
        intro: [{ max: 50, message: "最長50個字符", trigger: "blur" }]
      },
      // url中獲取 接單方 | 需求方
      category: "",
      // 查詢回來的廠區列表
      factory_list: null,
      // 查詢回來的次集團列表
      SECN_CMPY_list: null,
      // 查詢回來的事業群列表
      ENTRPS_GROUP_list: null,
      // 查詢回來的產品處列表
      PD_OFFICE_list: null,
      // 存儲頭像信息
      user_pic_file: null
    }
  },
  created() {
    // 獲取查詢參數值並存放在data中
    function getQuery(keyName) {
      var reg = new RegExp("(^|&)" + keyName + "=([^&]*)(&|$)"),
        r = window.location.search.substr(1).match(reg)
      if (r != null) return decodeURI(r[2])
      return null
    }
    this.category = getQuery("category")
    // console.log(this.category)
  },
  methods: {
    upImg() {
      // 觸發 ref='fileInp' 的點擊事件
      this.$refs.fileInp.dispatchEvent(new MouseEvent("click"))
    },
    getFile() {
      // console.log(this.$refs.fileInp.files[0])
      // 獲取到 input 選中的文件信息
      const inputFile = this.$refs.fileInp.files[0]

      // window 对象的 URL 对象通過 createObjectURL() 将blob或者file读取成一个url。
      const windowURL = window.URL || window.webkitURL
      if (inputFile) {
        if (
          inputFile.type !== "image/jpeg" &&
          inputFile.type !== "image/png" &&
          inputFile.type !== "image/gif"
        ) {
          alert("請上傳圖片文件")
          return
        }
        const dataURL = windowURL.createObjectURL(inputFile)
        this.form.logoUrl = dataURL

        // 上傳至服務器
        var data = new FormData()
        data.append('file', inputFile)
        data.append('file_type', 'user_pic')

        file_upload(data).then(res=>{
          if(res.code === "1"){
            console.log(res.t)
            this.user_pic_file = res.t
          }
        })
      }
    },

    // 查詢名稱是否重複
    ck_user(name, callback) {
      var data = { username: name }

      // 調用api接口
      ck_user_is_exist(data).then(res => {
        if (res.code == "0") {
          callback(new Error("賬戶已存在!"))
        } else {
          callback()
        }
      })
    },

    // 查询厂区列表
    _query_factory_list() {
      var data = {}
      query_factory_list(data).then(res => {
        if (res.code === "1") {
          this.factory_list = res.t
        } else {
          console.log("error")
        }
      })
    },

    // 查詢次集團信息
    _query_SECN_CMPY_list() {
      var data = this.form.factory
      query_SECN_CMPY_list(data).then(res => {
        if (res.code === "1") {
          this.SECN_CMPY_list = res.t
        } else {
          console.log("error")
        }
      })
    },

    // 查詢事業群列表信息
    _query_ENTRPS_GROUP_list() {
      var data = this.form.group
      query_ENTRPS_GROUP_list(data).then(res => {
        if (res.code === "1") {
          this.ENTRPS_GROUP_list = res.t
        } else {
          console.log("error")
        }
      })
    },

    // 查詢產品處列表信息
    _query_PD_OFFICE_list() {
      var data = this.form.bussiness
      query_PD_OFFICE_list(data).then(res => {
        if (res.code === "1") {
          this.PD_OFFICE_list = res.t
        } else {
          console.log("error")
        }
      })
    },

    submit(nameOne, nameTwo) {
      var obj = {
        username: this.form.name,
        password: this.form.psd,
        in_out_type: "in",
        // 接單方 | 需求方
        send_recv_type: this.category,
        fctry_zone: this.form.factory,
        secn_cmpy: this.form.group,
        entrps_group: this.form.bussiness,
        pd_office: this.form.bureau,
        dept_name: this.form.departName,
        legal_person: this.form.corporate,
        cost_code: this.form.costCode,
        send_mnufc_range: this.form.processRange,
        bank_name: "",
        bank_acunt: "",
        busis_mngr: this.form.manager,
        tel: this.form.tel,
        email: this.form.email,
        summary: this.form.intro,
        user_pic_file: this.user_pic_file
      }

      this.$refs[nameOne].validate(valid => {
        if (valid) {
          this.$refs[nameTwo].validate(valid => {
            if (valid) {
              register(obj).then(res => {
                if (res.code === "1") {
                  console.log(res.msg)
                } else {
                  alert("異常，請稍後重試！")
                }
              })
            } else {
              console.log("error submit!!")
              return false
            }
          })
        } else {
          console.log("error submit!!")
          return false
        }
      })
    },

    // 測試使用，生產刪除
    test() {
      // console.log(this.$md5(this.form.psd))
    }
  },
  components: {
    Top,
    Logo,
    Footer
  }
}
</script>

<style lang="less" scoped>
#content {
  background: #12222e url(../../assets/imgs/particulars/contentBG.png) no-repeat
    top center / contain;
  padding-top: 24px;
  // 邊界效果
  > .main {
    background: rgba(13, 28, 42, 0.2);
    box-sizing: border-box;
    padding: 20px;
    position: relative;
  }
  // 基本信息
  .general {
    height: 260px;
    padding: 20px 30px;
    background-color: #0096ff;
  }
  // 頂部信息
  .gen_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    span:nth-child(1) {
    }
    span:nth-child(2) {
      color: #9dccff;
      font-size: 12px;
    }
  }
  // 總額
  .gen_amount {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
    > span {
      color: #d9e9f3;
      font-size: 14px;
    }
  }
  // 具體數值
  .gen_am_number {
    margin-top: 8px;
    span:nth-child(1),
    span:nth-child(3) {
      font-size: 14px;
    }
    span:nth-child(2) {
      font-size: 48px;
    }
  }
  // 其餘總額相關信息
  .gen_am_info {
    margin-top: 36px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    color: #d9e9f3;
    font-size: 20px;
  }
  // 標語
  .slogan {
    width: 422px;
    height: 45px;
    background: url(../../assets/imgs/merchant/sloganBG.png) no-repeat;
    position: absolute;
    top: 297px;
    left: 50%;
    transform: translateX(-50%);
    color: #d3dfe7;
    font-size: 20px;
    text-align: center;
    line-height: 45px;
  }
  // 信息填寫框
  .formBox {
    background-color: #d3dfe7;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  // 信息框頂部
  .form_title {
    margin-top: 60px;
    text-align: center;
    > span {
      color: #0096ff;
      font-size: 18px;
    }
  }
  // 描述
  .tit_describe {
    margin-top: 26px;
    background: #c6d4de;
    text-align: center;
    width: 960px;
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    span:nth-child(1) {
      color: #12222e;
      font-size: 20px;
    }
    span:nth-child(2) {
      color: #50616f;
      font-size: 14px;
    }
  }
  // 信息框主體
  .form_info {
    width: 960px;
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    .formLeft,
    .formRight {
      width: 50%;
    }
  }
  // 修改 element 表格樣式
  // 單獨列
  /deep/ .el-form-item {
    display: flex;
    align-items: center;
    // logo
  }
  /deep/ .el-form-item.logo {
    .el-form-item__content {
      width: 326px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    span:nth-child(2) {
      color: #2e6e9e;
      font-size: 16px;
      border: 1px solid #afc2cf;
      display: inline-block;
      width: 58px;
      height: 24px;
      text-align: center;
      line-height: 24px;
      border-radius: 13px;
    }
    span:nth-child(3) {
      color: #7d919f;
      font-size: 14px;
    }
  }
  // label全局效果修改
  /deep/ .el-form-item__label {
    width: 100px !important;
    text-align: right;
    font-size: 16px;
    color: #212f3a;
    padding: 0;
  }
  // content全局效果修改
  /deep/ .el-form-item__content {
    font-size: 16px;
    color: #212f3a;
    margin-left: 10px !important;
  }
  /deep/ .el-input {
    input {
      background-color: #d3dfe7;
      border: 1px solid #afc2cf;
      border-radius: 0px;
      width: 326px;
      &:focus {
        border-color: #0092ff;
      }
    }
  }
  /deep/ .el-textarea {
    textarea {
      background-color: #d3dfe7;
      border: 1px solid #afc2cf;
      border-radius: 0px;
      width: 326px;
      height: 164px;
      &:focus {
        border-color: #0092ff;
      }
    }
  }
  // 信息框提交按鈕
  .form_chooseBtn {
    margin-top: 70px;
    margin-bottom: 40px;
    position: relative;
    cursor: pointer;
    > a {
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 20px;
        color: #fff;
      }
    }
  }
}
</style>